body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-position: relative;
padding: 0;
margin: 0;
}
.Footers{
  margin-top: 10%;
}
.images {
    width: 30%;
    height: 200px;
    box-shadow: 0 0 4px 1pt;
    width: 800px;
    height: 300px;
    margin-left: 4%;
}
.headerterss{
    float: right;
    font-style: italic;
    margin-top: 4%;
    color: skyblue;
    margin-right: 10%;
}
.conteints h5 {
    margin-left: 35%;
    padding: 2%;

}
.card {
    border: 1px solid skyblue;
    margin-left: 20%;
    background: skyblue;
    width: 200px;
    text-align: center;
    box-shadow: 5px 0 5px 5px;
    border: 2px solid black;
    
}
.card li{
    list-style-type: none;
}
.card img {
    border-radius: 50%;
    width: 100px;
    height: 80px;
    text-align: center;
}
.conteints {
    padding: 0%;
    margin: 0%;
    position: relative;
    margin-top: 10%;
    background: goldenrod;
    
}

.content {
    margin-bottom: 10%;
}
.Dashboard {
    background: whitesmoke;
    margin: 0%;
    padding: 0%;
    position: relative;
    
}
.service {
    text-align: center;
    margin-top: 10%;
position: relative;
margin-left: 40%;
color: black;

}
.service img {
    border-radius: 50%;
    height: 100px;
    width: 100px;
}

.cardservice {
    border: 2px solid black;
    border-radius: 50%;
    margin-top: 10px;
    box-shadow: 0 0 4px 1px;
}

.login {
    padding: 5%;
    margin-left: 30%;
    margin-top: 10%;

}
.loginnes{
    background: skyblue;
    width: 100%;
    height: 300px;
    position: relative;

}

.Toolbar {
    background: goldenrod;
    width: 100%;
    box-shadow: 0 0 3px 1px;
    box-sizing: border-box;
    position: relative;
}
.Toolbar h3 {
    color: skyblue;
    margin-left: 30%;
    margin-top: 10px;
}
.logo {
    border-radius: 50%;
    width: 50px;
    height: 60px;
    padding: 0%;
    float: left;
    box-shadow: 0 0 4px 1px;
}
.tools {
    float: right;
    height: 100%;
}
.keys {
    background-origin: border-box;
    color: black;
    text-align: center;
    font-size: 20px;
    margin-top: 5%;
   

}
.Footer{
    background: coral;
    width: 100%;
}

.gambarfooter{
    border-radius: 50%;
    text-align: center;
    width: 80px;
    height: 50px;
}
.tentang{
    margin-left: 30%;
opacity: 0.5;
}
.tentang li {
    list-style-type: none;
    margin-top: 5px;
}

.pendaftar form {
    width: 80%;
    margin-left: 30%;
}
.pendaftar {
    background: skyblue;
    margin-top: 10%;
    margin-bottom: 10%;
    box-shadow: 0 8px 3px 1px;
    box-sizing: border-box;

}
.Fruits {
    background: skyblue;
    margin-top: 3%;
    box-shadow: 0 2px 3px 1px;
    box-sizing: border-box;
   position: relative;
   padding: 5%;
    
}
.cardfruits{
    margin-left: 3%;
    background: white;
    text-align: center;
    list-style: none;
    font-size: 18px;
    box-shadow: 0 2px 4px 3px;
    box-sizing: border-box;
    padding-top: 5%;
    
}

.ffruits {
    font-display: inherit;
    text-align: center;
    margin-top: 3%;
    font-style: italic;
}
.ffruits h3 {
    color: skyblue;
}
.ffruits p {
    color: black;
    float: left;
}




.disable {
    text-align: center;
    font-size: 20px;
    color: brown;
    margin-top: 5%;
}
